@import "./../../styles/scss/";
.container {
  @include displayFlex($justify: space-between);
  .cardsContainer {
    @include displayFlex($wrap: wrap, $justify: flex-end);
  }
}

@media only screen and (min-width: 754px) {
  .container {
    .cardsContainer {
      justify-content: space-between;
    }
  }
}
